*{
    padding: 0;
    margin: 0;
}

html {
    width: 100%;
    height: 100%;
}
body{
    width: 100%;
    height: 100%;
    background-image: url(/assets/texture.png), linear-gradient(-135deg ,  rgba(0,0,0,1)  0%,  rgba(53,92,125,1) 100%);
}

.container{
    width: 540px;
    height: 450px;
    position: absolute;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    background-color: #d7fffd;
    margin:auto; 
    border: 2px solid #a1e6e3 ;
    border-radius: 10px;

}

.container.player{
    position: relative;
    background-color: #ffffff;
    width: 70px;
    height: 70px;
    border-radius: 45px;
}

.player.pink{
    background-color:#eca0b6;
}

.player.yellow{
    background-color: #fafba4;
}

.column:hover{
    background-color: #a1e6e3;
}

.container.column{
    display: flex;
    flex-direction: column;
}

.board{
    display: flex;
    justify-content: center;
    align-items:center;
}

.tittleGame{
    font-family:'Londrina Shadow', cursive;
    color:  #fafba4;
    font-size: 50px;
    text-align: center;
    padding-top: 10px;
}

